html,
body,
#root,
.app,
.body {
	height: 100%;
}

* {
	margin: 0;
	padding: 0;
}

.app {
	font-family: 'Open Sans', sans-serif;
}

.nav {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;

	padding: 10px;

	border-bottom: 2px solid #ffd31f;
	background: #fff4ca;
}

.body {
	display: -ms-flexbox;
	display: flex;

	-webkit-box-sizing: border-box;

	        box-sizing: border-box;
	height: calc(100% - 57px);
	padding: 10px;

	background-color: #fff;
}

.sidebar {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-preferred-size: 120px;
	    flex-basis: 120px;
	-ms-flex-pack: center;
	    justify-content: center;

	max-height: 120px;
	margin-right: 10px;
	padding: 10px;

	border: 2px solid #1f73ff;
	background: #cbdfff;
}

.sidebar .stats {
	text-transform: lowercase;

	font-size: 12px;
}

.content {
	-ms-flex: 1 1;
	    flex: 1 1;

	padding: 10px;

	border: 2px solid #8bc34a;
	background-color: #f1f8e9;
}

.user-avatar {
	width: 64px;
	height: 64px;

	border-radius: 50%;
}

.user-avatar.small {
	width: 32px;
	height: 32px;
}

.user-stats {
	width: 100%;
}

.user-stats > div:first-child {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: distribute;
	    justify-content: space-around;

	margin-bottom: 10px;
}



